
.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.inputfile + label {
  font-size: 1em;
  font-weight: 200;
  color: #fff;
  background-color: #000;
  display: inline-block;
  width: 100%;
  outline: 1px solid #000;
  cursor: pointer;
}
.inputfile:focus + label,
.inputfile + label:hover {
  background-color: #fff;
  color: #000;
}
.library {
  padding-left: 25px;
  padding-right: 25px;
  font-size: 20px;
  overflow-y: hidden;
}
.library .selected {
  font-weight: bold;
}
.library .item {
  cursor: pointer;
}
.library .item:hover {
  color: #808080;
}
.library .loading {
  color: #ff0035;
  font-weight: bold;
  -webkit-animation: loadAni 1s infinite;
          animation: loadAni 1s infinite;
}
@-webkit-keyframes loadAni {
0% {
    opacity: 0.25;
}
50% {
    opacity: 1;
}
100% {
    opacity: 0.25;
}
}
@keyframes loadAni {
0% {
    opacity: 0.25;
}
50% {
    opacity: 1;
}
100% {
    opacity: 0.25;
}
}
.library h2 {
  border-bottom: 1px solid rgba(0,0,0,0.2);
}
.pane-root {
  width: 400px;
  height: 500px;
  background-color: rgba(230,230,230,0.9);
  outline: 1px solid #000;
  box-shadow: 0px 0px 15px 5px;
  -webkit-transition: left 0.5s ease;
  transition: left 0.5s ease;
  top: 0;
  bottom: 0;
  position: relative;
}
.pane-root .title-root {
  color: #fff;
  font-weight: bold;
  background-color: #000;
  height: 60px;
  line-height: 60px;
  font-size: 39px;
  text-align: center;
  text-shadow: -1px 0 #111, 0 1px #111, 1px 0 #111, 0 -1px #555, 0px 3px 0px #aaa;
}
.pane-root .descriptor-root {
  box-shadow: 0px 3px 5px 1px rgba(0,0,0,0.4);
  height: 60px;
  text-align: center;
  font-size: 1.2em;
  color: #eee;
  background-color: #ff0035;
  overflow-y: hidden;
  line-height: 60px;
}
.pane-root .verticalSquish-enter-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.pane-root .verticalSquish-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.pane-root .verticalSquish-enter,
.pane-root .verticalSquish-leave-to {
  height: 0px;
}
.pane-root .row:first-child {
  border-bottom: 2px solid rgba(0,0,0,0.1);
}
.pane-root .conversion-table-root {
  margin-top: 50px;
  height: 200px;
  width: 100%;
  padding: 0px 40px;
  box-sizing: border-box;
}
.pane-root .conversion-table-root .row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 20px;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  width: 100%;
  height: 100px;
  line-height: 100px;
  margin-bottom: 15px;
  padding-bottom: 15px;
  text-align: center;
}
.pane-root .conversion-table-root .row .side {
  width: 45%;
}
.pane-root .conversion-table-root .row .side .itemType {
  font-size: 30px;
  font-weight: bold;
  color: #333;
}
.pane-root .conversion-table-root .row .divider {
  width: 10%;
  background-image: url("../assets/arrow_right.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 90%;
}
.pane-root .btn-upload-root {
  width: 200px;
  color: #fff;
  background-color: #000;
  height: 70px;
  position: absolute;
  bottom: -35px;
  left: 0;
  right: 0;
  margin: auto;
  top: 1;
  text-align: center;
  line-height: 70px;
  cursor: pointer;
}
.fade-enter-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.fade-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  height: 0;
}


