
#app {
  height: 100%;
}
body {
  padding: 0px 0px;
  margin: 0px 0px;
  background: #262626;
  background: -webkit-radial-gradient(center ellipse, #262626 0%, #000 100%);
  background: radial-gradient(ellipse at center, #262626 0%, #000 100%);
  width: 100vw;
  height: 100vh;
  font-family: 'Poppins', sans-serif;
}
.root {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: hidden;
}
.strip-top {
  height: 25px;
  top: 0px;
  position: absolute;
  width: 100%;
  background-color: #000;
}


