
.failed-enter-active,
.failed-leave-active {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.failed-enter {
  height: 0px;
}
.failed-enter-to {
  height: 50px;
}
.failed-leave {
  height: 50px;
}
.failed-leave-to {
  height: 0px;
}
#notificationCenter-root {
  box-sizing: border-box;
}
#notificationCenter-root .noti {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  width: 100%;
  line-height: 30px;
  background-color: rgba(255,255,255,0.5);
  box-sizing: border-box;
  box-shadow: 0px 2px 2px 2px rgba(0,0,0,0.2);
  margin-bottom: 2px;
  overflow-y: hidden;
}
#notificationCenter-root .noti .icon {
  margin-right: 15px;
  float: left;
  color: #fff;
  text-align: center;
  width: 35px;
  height: 35px;
  margin-top: 8px;
  margin-left: 8px;
  background-color: #ffa500;
  content: '';
  border-radius: 25px;
  font-weight: bold;
  font-size: 24px;
  line-height: 40px;
}
#notificationCenter-root .noti .title {
  font-weight: bold;
  line-height: 50px;
  height: 50px;
}


