
.fifty {
  max-width: 45%;
}
.fade-enter-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.inView {
  width: 810px;
}
.outOfView {
  width: 0px;
}
.loadedTitle {
  font-weight: bold;
}
.stage {
  height: 600px;
  width: 800px;
  overflow-y: hidden;
  overflow-x: hidden;
  margin: 15px auto;
  box-shadow: 0px 2px 7px 2px rgba(0,0,0,0.7);
  position: relative;
}
.stage .imageBox {
  background-size: contain;
  width: 800px;
  height: 600px;
  background-repeat: no-repeat;
  background-position: center;
}
.stage .loadingCover {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
}
.stage .loadingCover .loadingText {
  position: absolute;
  font-weight: bold;
  font-size: 32px;
  text-align: center;
  left: 0;
  right: 0;
  height: 50px;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 200px;
  border: 2px solid rgba(0,0,0,0.2);
  -webkit-animation: loading 1s infinite;
          animation: loading 1s infinite;
}
@-webkit-keyframes loading {
0% {
    opacity: 0.25;
}
50% {
    opacity: 0.75;
}
100% {
    opacity: 0.25;
}
}
@keyframes loading {
0% {
    opacity: 0.25;
}
50% {
    opacity: 0.75;
}
100% {
    opacity: 0.25;
}
}
.stage-root {
  margin-top: 20px;
  height: 750px;
  margin-left: 30px;
  -webkit-transition: opacity 0.5s ease, width 0.5s ease;
  transition: opacity 0.5s ease, width 0.5s ease;
  overflow-x: hidden;
  overflow-y: hidden;
}
.btn-download {
  background-color: #ff0035;
  color: #fff;
  height: 40px;
  float: left;
  padding: 5px 15px;
  line-height: 40px;
  cursor: pointer;
}
.btn-download:hover {
  background-color: #bf0028;
}
.statBox {
  width: 800px;
  overflow-x: hidden;
  overflow-y: hidden;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.statBox .stat {
  float: left;
  background-color: #fff;
}
.stat {
  margin-right: 15px;
  height: 40px;
  line-height: 40px;
  padding: 5px 15px;
  font-size: 20px;
}
.list-item {
  display: inline-block;
}
.list-enter-active,
.list-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.list-enter,
.list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}


